.card__item {
  display: flex;
  flex-direction: column; }

.card__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  border: 1px solid #DEE2E6;
  border-width: 0 1px 1px 1px;
  border-radius: 0 0 .25rem .25rem;
  box-shadow: 0px 2px 6px #00000029;
  color: #212529; }

.card__img {
  max-width: 100%;
  height: auto;
  border-radius: .25rem .25rem 0 0;
  object-fit: cover; }

.card__title {
  font-size: 0.875rem;
  line-height: 21px;
  margin-bottom: 0.5rem; }

.card__footer {
  font-size: 0.875rem;
  color: #707070;
  margin-top: 1rem; }

.form__text {
  font-size: 1.25rem;
  line-height: 1.875rem;
  margin-bottom: 1.25rem; }
