  @media (max-width: 767px) {
    .form__textarea {
      padding: .4375rem .75rem .5625rem .75rem; } }

.form__checkbox {
  display: none; }

.form__checkbox + .form__checkboxSpan {
  display: inline-block;
  background: url(../images/check_box_outline_blank_black_24dp.svg) no-repeat left top;
  cursor: pointer;
